.relative {
    position: relative;
}

.flex {
    display: flex;
}

.wrap {
    flex-wrap: wrap;
}
.center {
    justify-content: center;
    align-items: center;
}

.end {
    justify-content: end;
}
    
.gap1 {
    gap: 1em
}

.gap2 {
    gap: 2em
}
.between {
    justify-content: space-between;
}

.around {
    justify-content: space-around;
}
.grid {
    display: grid;
}

.columns {
    grid-auto-flow: column;
    flex-direction: column;
}

.rows {
    grid-auto-flow: row;
    flex-direction: row;
}

.ltr {
    direction: ltr;
}

.rtl {
    direction: rtl;
}

.square {
    border-radius: 2px;
}

.squarish {
    border-radius: 8px;
}
.round {
    border-radius: var(--themeBorderRadius);
}

.rounder {
    border-radius: 20px;
}

.border {
    border: 1px solid;
}

.small {
    font-size: 0.8em;
    }
    
.smaller {
    font-size: 0.6em;
}

.opacity {
    opacity: 0.7;
}
    
    .opacityHover:hover {
        opacity: 0.7;
}

.medium {
    font-size: 0.92em;
}

.large {
    font-size: 1.15em;
}

.xLarge {
    font-size: 1.3em;
}



.fit {
    width: fit-content;
}

.fill {
    width: fill-available;
    width: -webkit-fill-available;
}

.full {
    width: 100%;
}

.fitH {
    height: fit-content;
}

.pointer {
    cursor: pointer;
}

.tStart {
    text-align: start;
}

.tCenter {
    text-align: center;
}
.alignCenter {
    align-items: center;
    align-content: center;
    align-self: center;
}

.baseLine {
    align-items: baseline;
}
.boxShadow {
    box-shadow: var(--boxShadow);
}

.boxShadowHover {
    box-shadow: var(--boxShadow);
}

.boxShadowHover:hover {
    /* box-shadow: 2px 2px 12px 1px rgba(0, 0, 0, .955); */
    box-shadow: 0px 5px 4px 15px rgba(0, 0, 0, 0.599);
        box-shadow: 4px 14px 20px rgba(0, 0, 0, 0.5);
}

.translateY:hover {
    transform: translateY(-1.4em);
}

.dropShadow {
    -webkit-filter: drop-shadow(3px 5px 2px rgba(26, 54, 79, 0.7));
    filter: drop-shadow(3px 5px 2px rgba(26, 54, 79, 0.7));
}

.dropShadowWhite {
    -webkit-filter: drop-shadow(3px 5px 2px rgba(254, 248, 233, 0.4));
        filter: drop-shadow(3px 5px 2px rgba(254, 248, 233, 0.4));
}

.darkModeHeader svg,
.darkModeHeader div.subMenu,
div.darkModeHeader li.activePage,
div.darkModeHeader {
    background: black;
    background: var(--themeColorBG_DarkMode);
    color: var(--themeColorText_DarkMode) !important;
    fill: white !important;
}




.darkMode,
.darkMode svg {
    background: var(--themeColorBG_DarkMode) !important;
    color: var(--themeColorText_DarkMode) !important;
    fill: white !important;
        /* background: var(--themeDarkBlue) !important; */
        /* background-image: var(--headerGradient) !important; */
    }
    
    
    .darkModeGradient {
        color: var(--themeColorText_DarkMode) !important;
        fill: white !important;
        background: var(--themeDarkBlue) !important;
        background-image: var(--headerGradient) !important;
}

a,
a:hover,
a:visited,
a:active {
    color: inherit;
    list-style: none;
    text-decoration: none;
    transition: 0.2s;
    text-align: -webkit-center;
}

button {
    padding: 0.5em 0.6em;
    color: var(--themeColorText_DarkMode);
    background-color: var(--themeColor);
    border-radius: 0.2em;
    font-weight: bold;
    border: none;
    transition: 0.3s;
    font-size: 0.8em;
}

button:hover {
    opacity: 0.8;
}

.bStart {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

.bEnd {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}
.alpha {
    background-color: var(--themeColorAlpha);
}

.themeConst {
    background-color: var(--constThemeColor);
    color: white;
}

.themeConst2 {
    background-color: var(--ThemeGPTOrangeDeep);
    color: black;
}

.themeConst3 {
    background-color: var(--themeGrey);
    color: var(--themeColorText);
}

.themeConst3:hover {
    opacity: 0.7;
}

.themeConst4 {
    color: var(--themeColorText);
    background-color: transparent;
    color: inherit;
}

.themeConst4:hover {
    color: var(--themeColorText_DarkMode);
    background-color: var(--themeColor);
}

.themeBorder {
    background: transparent;
    color: var(--themeColorText);
    color: inherit;
    border: 1px solid var(--themeColor);
}

.themeBorder:hover {
    color: var(--themeColorText_DarkMode);
    background: var(--themeColor);
}

.themeConstBorder {
    background: transparent;
    color: var(--themeColorText);
    color: inherit;
    border: 1px solid var(--constThemeColor);
}

.themeConstBorder:hover {
    color: var(--themeColorText_DarkMode);
    background: var(--constThemeColorAlpha);
}

.rotate {
    animation: rotate 1s forwards;
}

.borderDropBR {
    border-radius: 30px 30px 1px 30px !important;
}

.borderDropBL {
    border-radius: 30px 30px 30px 1px !important;
}
.fadeIn {
    animation: translate 0.7s ease-in forwards;
}
@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}


@keyframes translate {
    0% {
        transform: translateY(2.4em);
        opacity: 0;
    }

100% {
    transform: translateY(0em);
    opacity: 1;
}
}







