@media only screen and (max-width: 1100px) {
    body {
        font-size: 16px;
        /* background: rgba(0, 128, 0, 0.346); */
    }

    .App {
        font-size: 0.95rem;
    }


    #frame2Card {
        height: 16em;
    }

    img#frame2Img {
        width: 4em;
        height: 4em;
    }

    img#tutorSelfie {
        margin: 1.5em !important;
            width: 10em !important;
            height: 10em !important;
    }
}

@media only screen and (max-width: 900px) {
    body {
        font-size: 14px;
        /* background: rgba(128, 107, 0, 0.346); */
    }

    div.frameDiv1,
    div.frameDiv3,
    #frame2Card * {
        font-size: 0.8em;
    }

    #frame2Card {
        height: 14em;
        width: 14em;
    }

    div.frameDiv3 h1 {
        font-size: 1.5em;
    }

    img#frame2Img {
        width: 3.5em;
        height: 3.5em;
    }

    img#tutorSelfie {
        margin: 2em;
        width: 9em;
        height: 9em;
    }

    div.frameDiv3 button {
        font-size: 0.85em !important;
    }

    button {
        font-size: 0.8em;
    }

    div.checkBoxDiv input[type=checkbox] {
        margin: 0.4em;
        width: 0.7em;
        height: 0.7em;
    }

}

@media only screen and (max-width: 800px) {
    body {
        font-size: 12px;
        /* background: rgba(102, 0, 128, 0.346); */
    }

    .App {
        font-size: 0.9rem;
    }

    div.frameDiv1,
    div.frameDiv3,
    #frame2Card * {
        font-size: 0.8em;
    }

    #frame2Card {
        height: 11em;
        width: 14em;
    }

    div.quickSignUp form div {
        flex-direction: column;
        align-items: center;

    }

    div.quickSignUp h3 {
        font-size: 0.9em;
    }

    div.quickSignUp form div input {
        width: 80%;
        margin: 1em;
    }

    div.quickSignUp form div input {
        margin: 0.5em 0.3em;
    }

    div.quickSignUp form div#checkBoxDiv,
    div.quickSignUp form div#checkBoxDiv div {
        flex-direction: row;
        font-size: 0.7em;
    }

    div.quickSignUp form button {
        font-size: 0.6em;
        margin: 1em !important;
    }

    div.quickSignUp form div input[type='checkbox'] {
        width: 1em;
    }

}

@media only screen and (max-width: 680px) {


    section.caruselDiv {
        background: var(--themeColorAlpha);
        padding: 1em 0.5em;
    }

    #frame2Card {
        height: 10em;
        min-width: 5em;
        margin: 0.7em;
    }

    div.frameDiv2 {
        display: flex;
        flex-wrap: wrap;
        width: -webkit-fill-available;
        margin: 0em;
    }

    div.frameDiv1,
    div.frameDiv3 {
        margin: 0em !important;
        border-radius: 0em !important;
    }

    div.quickSignUp {
        margin: 0em;
        border-radius: 0em;
    }

    div.frameDiv3 div h2 {
        font-size: 1em;
    }

    div.frameDiv3 p {
        font-size: 0.9em !important;
    }

    div.frameDiv3 h1 {
        font-size: 1.3em;
    }

    div.frameDiv3 svg#tutorSelfie {
        width: 4em !important;
        height: 4em !important;
        padding: 1em;
        margin: 1em;
    }

    div.writeMe {
        width: -webkit-fill-available;
        box-shadow: none;
    }

    div.writeMe div {
        flex-direction: column;
    }

    div.writeMe svg {
        width: 0.6em;
    }

    div.writeMe input,
    div.writeMe textArea {
        width: 65vw !important;
    }

    img#tutorSelfie {
        margin: 1em !important;
            width: 7em !important;
            height: 7em !important;
        border: 1px solid rgba(255, 255, 255, 0.651);
    }

    button {
        font-size: 0.8em;
    }

    div#promptContainer {
        top: 10%;
        width: 68vw;
        height: 80vh;
        left: 10%;
    }

    input#password,
    input#password2,
    input#email,
    input#Password,
    input#Email {
        width: 90% !important;
            max-width: none !important;
    }

    div.login,
    div.signup {
        width: -webkit-fill-available;
        border-radius: 0px !important;
    }

    input#lname,
    input#fname {
        min-width: 20vw;
    }

    div.loginForm button {
        font-size: 0.8em;
    }
    div.fliterDiv p {
        display: none;
    }
        div#promptContainer {
            left: 15% !important;
            width: 74vw !important;
        }

}

@media only screen and (max-width: 450px) {
    body {
        font-size: 8px;
        /* background: rgba(128, 53, 0, 0.346); */
    }

    .App {
        font-size: 0.9rem;
    }

    .translateY:hover {
        transform: none;
    }

    div.frameDiv2 {
        padding: 0.8em;
    }

    /* #frame2Card {
        height: 7em;
        max-width: 5em;
    */

    div#frame2Card h3 {
        font-size: 1em;
    }

    div.frameDiv3 h1 {
        font-size: 1.1em;
    }

    div.frameDiv3 p {
        font-size: 0.8em !important;
    }


    div.frameDiv3 svg#tutorSelfie {
        width: 3em !important;
        height: 3em !important;
    }

    div.frameDiv3 button {
        font-size: 0.6em !important;
    }

    button {
        font-size: 0.7em;
    }

    img#tutorSelfie {
        width: 5em !important;
        height: 5em !important;
    }
div#promptContainer {
    left: 10% !important;
    width: 80vw !important;
}
